.title-container {
  display: inline-block;
  width: 280px;
  margin: 0;
  padding: 5px;
}

.left-title, .right-title {
  display: inline-block;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.left-title {
  color: red;
  margin-left: -12px;
}

.right-title {
  padding-left: 2px;
}

.title-container:hover .left-title {
  font-size: 0.8em;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
}

.title-container:hover .right-title {
  padding-left: 0px;
  -webkit-transform: translateY(3px);
      -ms-transform: translateY(3px);
          transform: translateY(3px);
}

.title-container:hover svg>g {
  -webkit-animation: ease-in-out wiggle 0.5s;
          animation: ease-in-out wiggle 0.5s;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
  -webkit-transform-origin: 50%;
      -ms-transform-origin: 50%;
          transform-origin: 50%;
}

@-webkit-keyframes wiggle {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  30%  { -webkit-transform: rotate(8deg); transform: rotate(8deg); }
  60%  { -webkit-transform: rotate(-8deg); transform: rotate(-8deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes wiggle {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  30%  { -webkit-transform: rotate(8deg); transform: rotate(8deg); }
  60%  { -webkit-transform: rotate(-8deg); transform: rotate(-8deg); }
  100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

.logo-container {
  display: inline-block;
  position: relative;
  width: 40px;
}

.logo-container > svg {
  position: absolute;
  top: -25px;
  width: 35px;
  height: 35px;
  margin: 0;
}


