.frame-container {
  margin-top: 43px;
  height: calc(100% - 38px - 5px);
}

.file-display {
  margin-bottom: 5px;
  width: 100%;
}

.textframe {
  padding: 5px;
  height: calc(100% - 43px);
  overflow: auto;
  white-space: pre-line;
}

.default-msg {
  display: -ms-flexbox;
  display: flex;
  height: 60%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  font-weight: 100;
  font-size: 2em;
  color: rgba(0, 0, 0, 0.4);
}


